<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <div class="content">
                <div class="cam-container">
                    <div @click="triggerCamera" class="cam-button mb-3">
                        <svg-icon type="mdi" :path="path"></svg-icon>
                    </div>
                    <input type="file" name="photofile" id="photofile" accept="image/*" capture="camera"
                    @change="onPhotoFileChange">
                </div>
                <br>
                <div class="img-container text-center">
                    <img src="" id="photoimg">
                </div>
                <div class="button-container">
                    <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200" @click="submitResponse"></v-btn>
                </div>
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card class="pa-3">
                <v-card-text>
                <v-row align="center" justify="center">
                    <v-col align="center">
                    <div class="img-container text-center">
                        <img :src="uploadedPhoto" id="dialogPhoto" style="max-height:100%; max-width:100%;">
                    </div>
                    <br>
                    <div class="text-center">
                        <p>    <strong></strong>  .</p>
                        <p>       <strong>AI </strong>  .</p>
                    </div>
                    <div>
                        <div class="btn_container">
                            <v-btn @click="downloadImage" class="mr-3"></v-btn>
                            <v-btn @click="goToAiPainter" class="ml-3">AI </v-btn>
                        </div>
                        <div>
                            <v-btn @click="handleDialogConfirmation" class="mt-3" color="primary"> </v-btn>
                        </div>
                    </div>
                    </v-col>
                </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script >
import EventBus from '@/EventBus.js';
import SvgIcon from '@jamescoyle/vue-icon';
import { mdiCameraOutline } from '@mdi/js';

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result4: {
            type: Boolean,
        }
    },
    components:{
        SvgIcon
    },
    data(){
      return {
        userResult: '', //   
        dialog: false,

        userResponse:'',
        uploadedPhoto: null, //     

        path: mdiCameraOutline,
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        triggerCamera(){
            const photoFileInput = this.$el.querySelector("#photofile");
            photoFileInput.click();
        },
        onPhotoFileChange(event){
            let filename;
            if (window.FileReader) {
                filename = event.target.files[0].name;
            } else {
                filename = event.target.value.split('/').pop().split('\\').pop();
            }
            console.log("  : " + event.target.files[0].size);
            console.log(" : " + filename);
  
            this.previewImage(event.target);
  
              //    .
            this.uploadedPhoto = event.target.files[0];

              //    .
            const fileReader = new FileReader();
            fileReader.readAsDataURL(event.target.files[0]);
            fileReader.onload = () => {
                // const photoDataUrl = fileReader.result;
                // EventBus.$emit('add-photo', photoDataUrl);
                const photoDataUrl = fileReader.result;
                this.uploadedPhoto = photoDataUrl;
                EventBus.$emit('add-photo', photoDataUrl);
            }
        },
        previewImage(inputElement){
            if (inputElement.files && inputElement.files[0]) {
                let reader = new FileReader();
                reader.onload = (e) => {
                    this.$el.querySelector("#photoimg").setAttribute('src', e.target.result);
                    this.$el.querySelector("#photoimg").style.display = "block";
                }
                reader.readAsDataURL(inputElement.files[0]);
            }
        },
        submitResponse(){
            //   
            if (this.uploadedPhoto) {
                console.log(this.userResponse); //  
                this.userResponse = ''; //  

                this.dialog = true;

            } else {
                //     
                alert(" !");
            }
        },
        handleDialogConfirmation(){
            this.dialog = false;

            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = ''; //   
            this.closeP();        //  
        },
        goToAiPainter(){
            this.$router.push({name:'AIView'});
            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = '';
            this.closeP();

        },

        downloadImage() {
            if (this.uploadedPhoto) {
            //    
            const link = document.createElement('a');
            link.href = this.uploadedPhoto;

            //     (: downloaded-image.jpg)
            const filename = 'family_img.jpg';
            link.download = filename;

            //     
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            } else {
            //      (:    )
            alert('  !');
            }
        },

    }
}
</script>

<style scoped>
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .img-container{
        justify-content: center;
        display: flex;
        align-items: center;
    }
    #photoimg {
        display: none;
        width: 50%;
        height: auto; /*     */
        margin-top: 20px; /*    */
    }

    #photofile{
        display:None;
    }

    #dialogPhoto {
        width: 80%; /*    */
    }
    .cam-container {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px; /*    */
    }
    .button-container {
        width: 100%; /*    */
        padding: 10px 20px; /*    */
        display: flex; /*    */
        justify-content: center; /*   */
        margin-top: auto; /*         */
    }
    .white-bg{
        width: 90%; height: 90%;
        border-radius: 22px;
        position: fixed; 
        /* background-color: white; */
        overflow:hidden;
        display:flex;
        flex-direction: column; 
        align-items: flex-end;
        background-image: url('@/assets/mission/bg_4.png');
        background-size: cover; /*      */
        background-position: center; /*      */
        background-repeat: no-repeat; /*     */
      }

    .content{
        width:100%;
        position: absolute;
        bottom: 7%;
        max-height: 45%;
        overflow-y: auto;
    }

    .close img{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 3%;
        right: 5%;
    }
    .custom-submit-button {
        color: white !important; /*    */
        font-weight: bold; /*    */
        font-size: 18px; /*   18px  */
    }
</style>